import React from 'react'
import { Card, CardContent, Typography } from '@material-ui/core'
function Infobox({title, cases, total , Red, active, recovered, ...props}) {
  return (
    <Card onClick={props.onclick} className={`infoBox ${active && "infoBox--selected"} ${Red&&"red"} ` } >
        <CardContent>
            <Typography className="infoBox__title" color="textSecondary">
                {title}
            </Typography>
            <h2 className={`infoBox__cases ${recovered && "recovered"}`}>{cases}</h2>
            <Typography color="textSecondary">
                {total} Total
            </Typography>
        </CardContent>

    </Card>
  )
}

export default Infobox