import { Card, CardContent, FormControl, MenuItem, Select } from '@material-ui/core';
import './App.css';
import { useEffect, useState } from "react"
import Infobox from './infobox';
import Map from "./Map";
import Table from "./Table"
import { preetyPrintStat, sortData } from './util';
import LineGraph from './LineGraph';

import "leaflet/dist/leaflet.css"
function App() {
  const [countries, setCountries] = useState([])
  const [country, setCountry] = useState("worldwide")
  const [casesType,setCasesType] = useState("cases")
  const [countryInfo, setCountryInfo] = useState({})
  const [mapCenter, setMapCenter] = useState({ lat: 20.5937, lng: 78.9628 })
  const [zoom, setMapZoom] = useState(2)
  const [mapCountries, setMapCountries] = useState([])
  const [tableData, setTableData] = useState([])
  useEffect(async () => {
    await fetch("https://disease.sh/v3/covid-19/all")
      .then(res => res.json())
      .then(data => {
        setCountryInfo(data)
        
      })
  }, [])

  useEffect(() => {
    const getCounriesData = async () => {

      await fetch("https://disease.sh/v3/covid-19/countries")

        .then((response) => response.json())
        .then((data) => {
          const countries = data.map((country) => {
            return ({
              name: country.country,
              value: country.countryInfo.iso2
            })
          })
          const sortedData = sortData(data)
          setTableData(sortedData)
          setCountries(countries)
          setMapCountries(data)
        })
    }
    getCounriesData()
  }, [])
  useEffect(()=>{

  },[])
  const onCountryChange = async (event) => {
    const countryCode = event.target.value
    const url = countryCode === "worldwide" ? "https://disease.sh/v3/covid-19/all" : `https://disease.sh/v3/covid-19/countries/${countryCode}`

    await fetch(url)
    .then(response => response.json())
    .then(data => {
      if(countryCode==="worldwide")
      {
        setMapZoom(1)
      }else{
        setMapZoom(5)
      }
      countryCode==="worldwide"?setMapCenter(  [20.5937, 78.9628 ]):setMapCenter([data.countryInfo.lat, data.countryInfo.long])
      setCountry(countryCode)
      setCountryInfo(data)
      setMapZoom(4)
      })
  }
  return (
    <div className="App">
      <div className='app__left'>

        <div className="app__header">
          <h1>Covid-19 Tracker</h1>
          <FormControl className="app__dropdown">
            <Select
              className='app__dropdownmenu'
              variant="outlined"
              value={country}
              onChange={onCountryChange}
            >
              <MenuItem value="worldwide">Worldwide</MenuItem>
              {countries.map((country) => {
                return (<MenuItem key={country.value} value={country.value}>{country.name}</MenuItem>)
              })}
            </Select>
          </FormControl>
        </div>
        <div className='app__stats'>
          <Infobox Red={true} active={casesType==="cases"} onclick={(e)=>setCasesType("cases")} title={"Coronavirus Cases"} cases={preetyPrintStat(countryInfo.todayCases)} total={countryInfo.cases} />
          <Infobox recovered={true} active={casesType==="recovered"} onclick={(e)=>setCasesType("recovered")} title={"Recovered"} cases={preetyPrintStat(countryInfo.todayRecovered)} total={countryInfo.recovered} />
          <Infobox Red={true} active={casesType==="deaths"} onclick={(e)=>setCasesType("deaths")} title={"Deaths"} cases={preetyPrintStat(countryInfo.todayDeaths)} total={countryInfo.deaths} />
        </div>
        <div className='map-container'>
          <Map casesType={casesType} countries={mapCountries} center={mapCenter} zoom={zoom} />
        </div>
      </div>


      <Card className="app__right">
        <CardContent>
          <h3>Live Cases by Country</h3>
          <Table countries={tableData} />
          <h3>Worldwide new {casesType}</h3>
          <div className="app__linegraph">
            <LineGraph casesType={casesType} />
          </div>
        </CardContent>
      </Card>
      {/* Graph */}
      {/* <Charts/> */}
      {/* Map */}
    </div>

  );
}

export default App;
